@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
:root {
  /* Color */
  --deep-gray-color: #2f2f2f;
  --white-color: #fff;
  --black-color: #000;

  /* font */
  --font-ptSerif: "PT Serif", serif;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "PT Serif", serif;
  font-family: var(--font-ptSerif);
  background-color: #2f2f2f;
  background-color: var(--deep-gray-color);
}

.movies .movie {
  min-width: 500px;
  width: 45%;
  margin-bottom: 70px;
  padding: 20px;
  padding-bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: #fff;
  background-color: var(--white-color);
  font-weight: 300;
  font-size: var(--font-small);
  color: var(--light-gray2);
  box-shadow: 5px 10px 15px #000;
  box-shadow: 5px 10px 15px var(--black-color);
  border-radius: 5px;
  transition: -webkit-transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
}

.movies .movie:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.movies .movie img {
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  -webkit-transform: rotateX();
          transform: rotateX();
  border-radius: 5px;
  box-shadow: 5px 10px 15px var(--deep-gray);
}

.movies .movie .movie__title,
.movies .movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movies .movie .movie__title {
  margin-bottom: 5px;
  font-size: var(--font-medium);
  font-weight: bold;
  color: var(--deep-gray);
}

.movies .movie .movie__year,
.movies .movie .movie__genres {
  font-size: small;
}

.movies .movie .movie__genres {
  list-style: none;
  margin: 5px 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

.movies .movie .movie__genres li {
  margin-right: 10px;
  font-size: var(--font-small);
}

.movies .movie .movie__summary {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  overflow: hidden;
}

.movies .movie:nth-child(7) {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

:root {
  /* Color */
  --deep-gray: #2c2c2c;
  --deep-gray2: #2f2f2f;
  --white-color: #fff;
  --black-color: #000;
  --light-gray: #d2d2e2;
  --light-gray2: #b2b3bc;

  /* font */
  --font-ptSerif: "PT Serif", serif;

  /* Size */
  --font-medium: 16px;
  --font-small: 12px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "PT Serif", serif;
  font-family: var(--font-ptSerif);
  background-color: #2f2f2f;
  background-color: var(--deep-gray2);
}

html,
body,
.container,
#root {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 300;
  color: #fff;
  color: var(--white-color);
}

.movies {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
}

